.container {
    max-width: 1020px;
    display: grid;
    justify-items: center;
    row-gap: 30px;
    padding: 0 10px 50px 10px;
    margin: auto;
}

.bg-img {
    height: 200px;
    background: url(../assets/logo_mobile.png) no-repeat;
    background-position: center center;
    width: var(--mobile-width);
    margin-top: 30px;
    margin-bottom: -40px;
}

.search-wrap {
    width: var(--mobile-width);
    position: relative;
}

.search-input {
    width: 100%;
    padding: 14px 0;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    outline: none;
    text-indent: 33px;
}

.search-icon {
    position: absolute;
    top: 15px;
    left: 12px;
    color: rgba(0, 0, 0, 0.54);
}

.search-input:focus+.search-icon {
    opacity: 0;
}

.search-input:focus {
    box-shadow: 0 0 6px 0 var(--focus-color);
    border: 1px solid rgba(0, 0, 0, 0);
}

input::placeholder {
    color: rgba(0, 0, 0, 0.5);
    text-indent: 33px;
}

.character-img {
    max-width: 100%;
    width: var(--mobile-width);
    height: auto;
}

.character-grid {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    gap: 20px;
}

.character-item {
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 4px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.character-name {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    color: rgba(0, 0, 0, 0.87);
    padding: 10px;
}

.character-specie {
    font-size: 14px;
    line-height: 21px;
    color: rgba(0, 0, 0, 0.6);
    padding: 10px;
    margin-top: -30px;
}


/* Tablet Breakpoint */

@media only screen and (min-width: 768px) {

    .character-grid {
        grid-template-columns: repeat(2, 1fr);
    }

    .search-wrap {
        width: 644px;
    }
}


/* Small Desktop Breakpoint  */

@media only screen and (min-width: 992px) {

    .bg-img {
        background: url(../assets/logo_desktop.png) no-repeat;
        width: 600px;
        margin-bottom: 0;
    }

    .character-grid {
        grid-template-columns: repeat(3, 1fr);
        margin-top: 30px;
    }

    .character-img,
    .character-item {
        max-width: 240px;
    }

    .character-name {
        transition: var(--hover-transition);
    }

    .character-name:hover {
        color: var(--hover-color);
    }

    .search-wrap {
        width: 760px;
    }

    input::placeholder,
    .search-input {
        text-indent: 38px;
    }
}


/* Large Desktop Breakpoint */

@media only screen and (min-width: 1200px) {

    .character-grid {
        grid-template-columns: repeat(4, 1fr);
    }

    .search-wrap {
        width: 100%;
    }
}