.container-detailed {
    padding-left: 25px;
    gap: 10px;
}

.btn-back {
    background-color: transparent;
    border: none;
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
    margin-top: 20px;
    cursor: pointer;
    justify-self: start;
    padding-left: 30px;
    position: relative;
}

.btn-back::before {
    content: '';
    display: block;
    position: absolute;
    background: url(../assets/arrow_back.svg) no-repeat;
    width: 16px;
    height: 16px;
    top: 2px;
    left: 5px;
}

.character-img__detailed {
    max-width: 100%;
    width: 150px;
    height: auto;
    border: 5px solid #F2F2F7;
    border-radius: 50%;
    margin-top: 50px;
}

.character-title {
    font-size: 32px;
    line-height: 38px;
    color: #081F32;
    margin-top: 10px;
}

.character-info {
    justify-self: start;
    padding-left: 5px;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #8E8E93;
}

.character-description {
    justify-self: start;
    padding-left: 25px;
    padding-right: 10px;
    max-width: 415px;
    width: 100%;
}

.character-feature {
    font-weight: 700;
    color: #081F32;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    width: 100%;
}

.character-span {
    display: block;
    font-weight: 400;
    font-size: 14px;
    color: #6E798C;
    margin: 5px 0 10px 0;
}


@media only screen and (min-width: 768px) {

    .character-info,
    .character-description {
        justify-self: initial;
    }
}


@media only screen and (min-width: 992px) {

    .btn-back {
        transition: var(--hover-transition);
    }

    .btn-back:hover {
        color: var(--hover-color);
    }

    .character-img__detailed {
        width: 300px;
    }

    .character-title {
        font-size: 48px;
        line-height: 56px;
    }
}