@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import url(./styles/homepage.css);
@import url(./styles/character.css);
@import url(./styles/loading.css);

:root {
    --hover-color: #2F96A7;
    --focus-color: #8AEA92;
    --hover-transition: all 0.4s;
    --mobile-width: 312px;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
}

h1,
h2,
h3 {
    margin-top: 0;
}

a {
    text-decoration: none;
}